@import "../mixin.scss";
.login__title {
    text-align: center;
    font-size: 24px;
    line-height: 29px;
    margin: 0 0 50px 0;
}

.form__label {
    text-align: left;
}

.login__inputs {
    border: none;
    margin-bottom: 20px;
    display: flex;
    flex-direction: column;
    gap: 10px
}

.login__infotolltip{
    margin-bottom: 25px
}
.login__button {
    @include regularText;
    font-size: 16px;
    line-height: 40px;
    color: #ffffff;
    width: 100%;
    padding: 0;
    transition: 0.5s;
    align-self: center;
    cursor: pointer;
    background: #118dff;
    border-radius: 8px;
    border: none;
  }
  
  .login__button:hover {
    @include hover;
  }

.form__submit:disabled {
    width: 100%;
    padding: 0;
    text-align: center;
    border: none;
    background: #9b9a9a;
    border-radius: 3px;
    font-weight: 500;
    font-size: 14px;
    line-height: 45px;
    color: #C2C2C2;
    margin-bottom: 16px;
    cursor: default;
}

.form__submit:hover {
    opacity: .6;
}

.form__submit:hover:disabled {
    opacity: 1;
}

