.login{
        max-width: 880px;
        margin: auto;
        margin-top: 60px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        gap: 15px;
}

.login__form {
    width: 358px;
    margin: auto;
    border: 0;
}

.login__entry {
    transition: .5s;
 
    cursor: pointer;
    background-color: transparent;
    color: #000000;
    font-family: 'Inter';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    text-align: center;
    border: none;
    margin: 0 auto;
    text-decoration: none;
}

.login__entry:hover {
    opacity: 0.6;
}
